
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Dosis', sans-serif;
}
::-moz-selection {
  color: white;
  background: #f44336;
}
::selection {
  color: white;
  background: #f44336;
}
.navigation {
  height: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding: 0 30px;
  width: 100%;
  position: absolute;
}
.navigation a {
  display: -ms-flexbox;
  display: flex;
  opacity: .3;
  transition: .2s;
  color: white;
}
.navigation a:not(:last-child) {
  margin-right: 20px;
}
.navigation a:focus {
  outline: none;
}
.navigation a:focus svg {
  box-shadow: 0 0 0 6px rgba(255, 255, 255, 0.2);
}
.navigation a:hover,
.navigation a:focus {
  opacity: 1;
}
.navigation a svg {
  height: 32px;
  border-radius: 50%;
  box-shadow: 0;
  fill: #f0f0f0;
  transition: .2s;
}
.header {
  background: #272727;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 600px;
  height: 100vh;
  color: #f0f0f0;
}
.header p {
  font-size: 1.2em;
}
.header h1 {
  font-size: 2.75em;
  font-weight: normal;
  margin: 0;
}
